                                                    class="text-dark text-right font-weight-bold align-middle border-bottom-0">
                                                    {{$t('ShippingCharges')}} :</td>
                                                <td colspan="2" class="border-bottom-0"><input class="form-control"
                                                        type="text" placeholder="0.00"
                                                        v-model="ProductSummary.ShippingCharges"></td>
                                            </tr>
                                            <tr>
                                                <td colspan="7"
                                                    class="text-dark text-right font-weight-bold align-middle border-bottom-0">
                                                    {{$t('Adjustments')}} :</td>
                                                <td colspan="2" class="border-bottom-0"><input class="form-control"
                                                        type="text" placeholder="0.00"
                                                        v-model="ProductSummary.Adjustments"></td>
                                            </tr>

                                            <tr>
                                                <td colspan="7"
                                                    class="text-dark text-right font-weight-bold align-middle border-bottom-0">
                                                    {{$t('Total')}} :</td>
                                                <td colspan="2" class="border-bottom-0"><span
                                                        v-bind:value="summaryTotal">{{ProductSummary.SummaryTotal}}</span>
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div> 
                        <div class="row flex-row-reverse mt-2">
                            <div class="col-lg-6 text-right">
                                <a href="javascript:;" 
                                    v-bind:class="{disabled: submited}" 
                                    class="btn btn-warning mr-2" v-if="statusId != 1001" v-on:click="SaveAsDraft('D')" 
                                    :title="$t('SaveAsDraft')"><em class="fa fa-save pr-2"></em>{{$t('SaveAsDraft')}}</a>
                                <a href="javascript:;"
                                    data-tour="step: 1; title: enter  dealDetail; content: AddVendorDetailContent"
                                    v-bind:class="{disabled: submited}" v-on:click="SubmitData('A')"
                                    class="btn btn-success mr-2 addQuote_a" title="Save">
                                    <em class="fa fa-save pr-2"></em>{{$t('Save')}}
                                </a>
                                <a href="javascript:void(0)" class="btn btn-danger" title="Cancel" @click="onCancel">
                                    <em class="fa fa-close pr-2"></em>{{$t('Cancel')}}
                                </a>
                            </div>
                            <div class="col-lg-6">
                                <div class="py-2">
                                    <small class="text-danger">{{$t('MandatoryString')}}</small>
                                </div>
                            </div>
                        </div>                     
                    </Form>
                    <modalpricebook v-if="showPricebookModal" :selectedItemIndex="selectedItemIndex" :summaryData="ProductSummary.SummaryData" 
                    @close="showPricebookModal = false" @SetPriceBook="SetPriceBook"></modalpricebook>
                </div>
            </div>
        </section>
    </div>
</template>
<script>
import LeadDataService from "../../../services/DataService";
import modalpricebook from '../../../components/Common/Modal/modalpricebook.vue';
var ajaxRequestComplete = true;
export default {
    components: {
        modalpricebook
    },
    data(){
        return{
            FormSchema: [],
            quoteID: this.$route.params.id,            
            reqFromId: this.$route.params.reqFromId,            
            CountryData: [],
            Ischeckshipping: false,
            sameShippingAddressAsBilling: false,
            IsInventory: true,
            isTaxNameFilled: false,
            buttons: [{
                    type: "submit",
                    class: "btn btn-warning save",
                    text: "<i class='fa fa-save pr-2'></i>Save As Draft",
                    value: "D",
                    isVisible:false,
                    onClick: this.saveForm
                },
                {
                    type: "submit",
                    class: "btn btn-success save",
                    isVisible:false,
                    text: "<i class='fa fa-save pr-2'></i>Submit",
                    onClick: this.saveForm
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    isVisible:false,
                    text: "<i class='fa fa-close pr-2'></i> Cancel",
                    onClick: this.Close
                }
            ],
            dateformat:null,
            userId:null,
            companyId:null,
            isLoading:false,            
            qlist: '',
            showPricebookModal: false,
            submited: false,            
            statusId: 0,            
            selectedQuotationType: '0',
            ProductSummary: null,
        }
    },
    computed: {
        summaryTotal: function () {
            let total = 0;
            var data = this.ProductSummary.SummaryData;
            for (let i = 0; i < data.length; i++) {
                if (data[i].ProductRate > 0) {
                    total = parseFloat(total, 10) + parseFloat(data[i].ProductItemTotal, 10);
                }
            }
          total = parseFloat(total, 10).toFixed2();
            this.ProductSummary.SubTotal = total;
            if (this.ProductSummary.ShippingCharges > 0) {
                total = parseFloat(total, 10) + parseFloat(this.ProductSummary.ShippingCharges, 10);
            }
            if (this.ProductSummary.Adjustments > 0) {
                total = parseFloat(total, 10) - parseFloat(this.ProductSummary.Adjustments, 10);
            }
            total = parseFloat(total, 10).toFixed2();
            this.ProductSummary.SummaryTotal = total;
            return total;
        },
    },
    created:function(){
        this.userId= this.GetUserInfo.ID;
        this.companyId= this.GetUserInfo.CompanyId;
        this.dateformat= this.GetUserInfo.DateFormat;
        this.currency=this.GetUserInfo.Currency;
        if(this.currency){
            this.currency=(this.currency == "(USD)" ? "$" : this.currency);
        }
        Number.prototype.toFixed2 = function () {
            var amount = Math.floor(parseFloat(this, 10) * 100) / 100;
            return parseFloat(amount, 10).toFixed(2);
        };
        this.FetchData();
        this.GetCountryData('', 'COUNTRIES');
        this.qlist = this.$route.params.reqFromId;
    },
    methods:{
        onCancel: function () {
            if(this.reqFromId!="0"){
                this.$router.push({path:`/CRM/ViewDealDetails/${this.reqFromId}/CRM/CRM_DEALS`});
            }else{
                this.$router.push({ name: 'CRMQuote' });
            }
        },
        clearRateIfNameEmpty: function (item, type, isInPercentage) {
            if ($.trim(item.TAX_NAME) == '') {
                item.TAX_RATE = 0;
                item.TAX_NAME = $.trim(item.TAX_NAME)
            } else {
                this.isTaxNameFilled = false;
            }
        },
        outside: function (e) {
        },
        onlyDecimal: function (item, type, isInPercentage) {
            if (type == 'TAX') {
                if (item.TAX_NAME != '') {
                    item.TAX_RATE = item.TAX_RATE.replace(/[^0-9.]/g, '').replace(/(\..*)\./g, '$1');
                    if (isInPercentage && parseFloat((item.TAX_RATE != '') ? item.TAX_RATE : 0) > 100) {
                        item.TAX_RATE = '100';
                    }
                } else {
                    this.isTaxNameFilled = true;
                    item.TAX_RATE = "0";
                }
            } else if (type == 'DISCOUNT') {
                item.ProductDiscountValue = item.ProductDiscountValue.replace(/[^0-9.]/g, '').replace(/(\..*)\./g, '$1');
                if (isInPercentage && parseFloat((item.ProductDiscountValue != '') ? item.ProductDiscountValue : 0) > 100) {
                    item.ProductDiscountValue = '100';
                }

            }
            else if (type == 'Adjustments') {
                if (item.Adjustments.charAt(0) == '.') {
                    item.Adjustments = ('0' + item.Adjustments);
                }
            }
            else if (type == 'Shipping') {
                if (item.ShippingCharges.charAt(0) == '.') {
                    item.ShippingCharges = ('0' + item.ShippingCharges);
                }
            }
        },
        FetchData:function(){
            var vm = this;
            vm.isLoading=true;
            var BillingAddress = {
                BillingContactPerson: '',
                BillingCompanyName: 0,
                BillingAccountAddress: '',
                BillingAccountAddress2: '',
                BillingAccountCity: '',
                BillingCity: '',
                BillingCityList: [],
                BillingZipCode: '',
                BillingZipCodeList: [],
                BillingState: '',
                BillingStateList: [],
                BillingCompanyNameList: [],
                BillingCountry: '',
            };
            var ShippingAddress = {
                ShippingAccountName: '',
                ShippingCompanyName: '',
                ShippingContactPerson: '',
                ShippingAccountAddress: '',
                ShippingAccountAddress2: '',
                ShippingAccountCity: '',
                ShippingCity: '',
                ShippingCityList: [],
                ShippingZipCode: '',
                ShippingZipCodeList: [],
                ShippingState: '',
                ShippingStateList: [],
                ShippingCompanyNameList: [],
                ShippingCountry: '',
            };
            vm.Address = {
                BillingAddress: BillingAddress,
                ShippingAddress: ShippingAddress
            }
            var productSummary = [{
                ProductName: '',
                ProductList: [],
                ProductDescription: '',
                ProductQuantity: 0,
                ProductInStock: 0,
                ProductRate: 0,
                ProductTax: 0,
                ProductAmount: 0,
                ProductDiscount: 0,
                ProductDiscountType: 'percent',
                ProductDiscountValue: 0,
                ProductItemTotal: 0,
                PriceBookId: 0,
                toggleDiscount: false,
                toggleTax: false,
                TaxData: [],
                DiscountData: [],
            }];
            vm.ProductSummary = {
                SubTotal: 0,
                ShippingCharges: 0,
                Adjustments: 0,
                RoundOff: 0,
                SummaryTotal: 0,
                SummaryData: productSummary
            }          
            var Deal_id = vm.reqFromId;
            var AccountD = {};
            var DealD = {};
            const params = `id=${vm.$route.params.id}&moduleName=CRM&subModuleCode=CRM_QUOTES&langCode=en`;            
            vm.quoteID=String(vm.quoteID);  
            var quotationType = 1;         
            LeadDataService.GetDynamicForm(params).then(function (response) {
                if (response != null && response.data != null) {
                    //debugger
                    vm.isLoading = false;
                    vm.FormSchema = vm.$refs.ManageQuoteForm.ConvertCustomFieldIntoDynamicFormObj(response.data.data);                 
                    vm.FormSchema.forEach((group, gi) => {
                        group.Data.forEach(async (field, fi) => {   
                            if(field.name=="account_id"){
                                field.config.options=[];
                                field.config.onSearchChange = vm.onSearchh;
                            }                         
                            if(field.name == "order_number"){
                               field.value = 'Quotes Order number generated automatically.';
                               field.disabled=true;
                            }
                            if (field.name == 'quotation_type') {
                                vm.selectedQuotationType = field.value;
                                if (vm.reqFromId !="0") {
                                    field.value = 2;
                                    quotationType = 2;
                                    vm.selectedQuotationType = field.value;
                                }
                            } 
                            if (field.name == 'client_id') {
                                field.config.options = [];
                            }
                            if (field.name == 'contact_id') {
                                field.config.options = [];
                            }
                            if (field.name == 'deal_id') {
                                field.config.options = [];
                                if (vm.reqFromId !="0" && quotationType == 2) {
                                    // item.dependent_show_type = 1;
                                }
                            }                            
                            if (field.name == "deal_id" && vm.quoteID !="0") {
                                vm.dealid = field.value;
                            }
                            if (field.name == "account_id" && vm.quoteID  !="0") {
                                vm.accntid = field.value;
                            }
                            if (field.name == "account_id" && vm.quoteID == "0") {
                                AccountD = field;
                            }
                            if (field.name == "deal_id" && vm.quoteID == "0") {
                                DealD = field;
                            }  
                            if (field.name == "contact_id" && vm.quoteID !="0") {
                                var newSelect = field.config.options.reduce(function (memo, e1) {
                                    var matches = memo.filter(function (e2) {
                                        return e1.name == e2.name && e1.value == e2.value
                                    })
                                    if (matches.length == 0)
                                        memo.push(e1)
                                    return memo;
                                }, []);
                                field.config.options = [];
                                field.value = [];
                                field.config.options = newSelect;
                                field.value = newSelect;                                
                            }                         
                            field.config.onChange = vm.onChangeEventForFields;
                        });
                    });
                    if (vm.quoteID == "0") {
                        vm.IsInventory = response.data.INVENTORY_APPLICABLE == "no" ? false : true;
                    }
                    else {
                        vm.IsInventory = response.data.INVENTORY_APPLICABLE == "no" ? false : true;
                    }
                    if (vm.quoteID !="0") {
                        var addressData = response.data.addressData;
                        var chkBillingAddress = $.grep(addressData, function (element, index) {
                            return element.address_type == 'B';
                        });
                        var billingAddress = null;
                        if (chkBillingAddress.length > 0) {
                            billingAddress = {
                                BillingCompanyName: chkBillingAddress[0].address_name,
                                BillingContactPerson: chkBillingAddress[0].contact_name,  
                                BillingAccountAddress: chkBillingAddress[0].account_address,
                                BillingAccountAddress2: chkBillingAddress[0].account_address2,
                                BillingAccountCity: chkBillingAddress[0].city,
                                BillingCity: chkBillingAddress[0].county,
                                BillingCityList: vm.MakeNormalArray(chkBillingAddress[0].county_list),
                                BillingZipCode: chkBillingAddress[0].zip_code,
                                BillingZipCodeList: vm.MakeNormalArray(chkBillingAddress[0].zipcode_list),
                                BillingState: chkBillingAddress[0].state,
                                BillingStateList: vm.MakeNormalArray(chkBillingAddress[0].state_list),
                                BillingCompanyNameList: vm.MakeNormalArray(chkBillingAddress[0].address_list),
                                BillingCountry: chkBillingAddress[0].country
                            }
                            vm.ShippingContactPerson = vm.MakeNormalArray(chkBillingAddress[0].contact_person_list);
                        }
                        var chkShippingAddress = $.grep(addressData, function (element, index) {
                            return element.address_type == 'S';
                        });
                        var shippingAddress = null;
                        if (chkShippingAddress.length > 0) {
                            shippingAddress = {
                                ShippingCompanyName: chkShippingAddress[0].address_name,
                                ShippingContactPerson: chkShippingAddress[0].contact_name,  
                                ShippingAccountAddress: chkShippingAddress[0].account_address,
                                ShippingAccountAddress2: chkShippingAddress[0].account_address2,
                                ShippingAccountCity: chkShippingAddress[0].city,
                                ShippingCity: chkShippingAddress[0].county,
                                ShippingCityList: vm.MakeNormalArray(chkShippingAddress[0].county_list),
                                ShippingZipCode: chkShippingAddress[0].zip_code,
                                ShippingZipCodeList: vm.MakeNormalArray(chkShippingAddress[0].zipcode_list),
                                ShippingState: chkShippingAddress[0].state,
                                ShippingStateList: vm.MakeNormalArray(chkShippingAddress[0].state_list),
                                ShippingCompanyNameList: vm.MakeNormalArray(chkShippingAddress[0].address_list),
                                ShippingCountry: chkShippingAddress[0].country
                            }
                            vm.ShippingContactPerson = vm.MakeNormalArray(chkShippingAddress[0].contact_person_list);
                            vm.Ischeckshipping = true;
                            $("#chkshippingbox").attr("checked", true);
                            vm.sameShippingAddressAsBilling=true;
                            if (chkShippingAddress[0].contact_name == "" && chkShippingAddress[0].account_address == "" && chkShippingAddress[0].account_address2 == "" && chkShippingAddress[0].city == "" && (chkShippingAddress[0].zip_code == "" || chkShippingAddress[0].zip_code == 0) && (chkShippingAddress[0].state == "" || chkShippingAddress[0].state == 0) && (chkShippingAddress[0].country == "" || chkShippingAddress[0].country == null)) {
                                vm.Ischeckshipping = false;
                                $("#chkshippingbox").attr("checked", false);
                            }
                        }
                        vm.Address = {
                            BillingAddress: billingAddress,
                            ShippingAddress: shippingAddress
                        }
                        vm.checkForSameBillingAndShippingAddress();
                      
                        var productSummary = [];
                        var productSummaryAmount = response.data.productSummaryAmount;
                        var productSummaryList = response.data.productSummary;
                        vm.IsInventory = productSummaryList[0].INVENTORY_APPLICABLE == "no" ? false : true;
                        $.each(productSummaryList, $.proxy(function (index, item) {
                            item.product_name=item.product_name==null?[]:item.product_name;
                            var productDetails=vm.MakeNormalArray(item.product_name);   
                            var productDetail= productDetails;
                            var obj = {
                                ProductName:productDetail[0],
                                ProductList:productDetail,
                                ProductDescription: item.item_product_desc,
                                ProductQuantity: item.item_product_Quantity,
                                ProductInStock: (parseInt(item.inventory_available_quantity)),//+ parseInt(item.item_product_Quantity)),
                                ProductRate: item.item_product_list_price,
                                ProductTax: 0,
                                ProductAmount: item.item_product_amount,
                                ProductDiscount: item.item_product_discount,
                                ProductDiscountType: item.item_product_discount_type,
                                ProductDiscountValue: item.item_product_discount_value,
                                ProductItemTotal: item.item_total,
                                PriceBookId: item.item_price_book_id,
                                toggleDiscount: false,
                                toggleTax: false,
                                TaxData: (item.tax_data != null) ? vm.MakeNormalArray(item.tax_data) : [],
                                DiscountData: [],
                            };
                            console.log(obj)
                            productSummary.push(obj);
                        }, vm));
                        vm.ProductSummary = {
                            SubTotal: productSummaryAmount[0].summary_sub_total,
                            ShippingCharges: productSummaryAmount[0].summary_shipping_charges,
                            Adjustments: productSummaryAmount[0].summary_adjustments,
                            SummaryTotal: productSummaryAmount[0].summary_total,
                            SummaryData: productSummary
                        }
                    }
                    else {                        
                        if ($.trim(Deal_id) != "0") {
                            const url = `companyId=${vm.companyId}&userId=${vm.userId}&sourceid=${Deal_id}`;
                            LeadDataService.GetQuoteProductbyDeal(url).then(response1 => {
                              var productSummary = [];
                                var productSummaryList = [];
                                productSummaryList = response1.data==''?[]: response1.data.DATA;
                                var billAddress=response1.data==''?null:response1.data.hasOwnProperty('BillAddress')?response1.data.BillAddress.length>0?response1.data.BillAddress[0]:null:null;
                                if (productSummaryList.length == 0) {
                                    var selectListAcc = [];
                                    selectListAcc.push(vm.MakeNormalArray(response1.data.AccountSelect[0]));
                                    AccountD.select_options = selectListAcc,
                                    AccountD.select_value = AccountD.select_options;
                                    AccountD.value =response1.data.AccountId?selectListAcc.find((itm) => itm.value==response1.data.AccountId[0]):AccountD.select_options[0]; //String(response1.data.AccountId[0]);
                                    vm.FillDropdown('ACCOUNT_CONTACTS', AccountD.value.value);
                                    vm.FillDropdown('ACCOUNT_DEALS', AccountD.value.value);
                                    vm.FillDropdown('ACCOUNT_CLIENTS', AccountD.value.value);
                                    vm.FillDropdown('ADDRESS_NAMES', AccountD.value.value);
                                    vm.FillDropdown('BillingState', billAddress.account_country);
                                    DealD.select_options = response1.data.DealSelect[0];
                                    DealD.select_value = DealD.select_options;
                                    DealD.value =String(response1.data.DealId[0]);
                                    vm.Address.BillingAddress.BillingAccountAddress =billAddress==null?null: billAddress.account_address;
                                    vm.Address.BillingAddress.BillingAccountAddress2 =billAddress==null?null:billAddress.account_address2;
                                    vm.Address.BillingAddress.BillingAccountCity = billAddress==null?null:billAddress.account_city;
                                    vm.Address.BillingAddress.BillingZipCode =billAddress==null?null: billAddress.account_zip_code;
                                    vm.Address.BillingAddress.BillingCountry =billAddress==null?null:billAddress.account_country;
                                    vm.Address.BillingAddress.BillingState = billAddress==null?null:billAddress.account_state;
                                    vm.accntid = billAddress==null?null:billAddress.accid;
                                }
                                else {
                                    var selectListAcc = [];
                                    selectListAcc.push(vm.MakeNormalArray(productSummaryList[0].AccountSelect));
                                    AccountD.select_options = selectListAcc;
                                    AccountD.select_value = AccountD.select_options;
                                    AccountD.value =response1.data.AccountId?selectListAcc.find((itm) => itm.value==response1.data.AccountId[0]):AccountD.select_options[0];//String(productSummaryList[0].AccountId);
                                    vm.FillDropdown('ACCOUNT_CONTACTS', AccountD.value.value);
                                    vm.FillDropdown('ACCOUNT_DEALS', AccountD.value.value);
                                    vm.FillDropdown('ACCOUNT_CLIENTS', AccountD.value.value);
                                    vm.FillDropdown('ADDRESS_NAMES', AccountD.value.value);
                                    vm.FillDropdown('BillingState', billAddress.account_country);
                                    DealD.select_options = productSummaryList[0].DealSelect;
                                    DealD.select_value = DealD.select_options;
                                    DealD.value =String(productSummaryList[0].DealId);
                                    vm.Address.BillingAddress.BillingAccountAddress =billAddress==null?null: billAddress.account_address;
                                    vm.Address.BillingAddress.BillingAccountAddress2 = billAddress==null?null:billAddress.account_address2;
                                    vm.Address.BillingAddress.BillingAccountCity = billAddress==null?null:billAddress.account_city;
                                    vm.Address.BillingAddress.BillingZipCode =billAddress==null?null: billAddress.account_zip_code;
                                    vm.Address.BillingAddress.BillingCountry = billAddress==null?null:billAddress.account_country;
                                    vm.Address.BillingAddress.BillingState =billAddress==null?null: billAddress.account_state;
                                    vm.accntid =billAddress==null?null: billAddress.accid;
                                    vm.IsInventory = productSummaryList[0].INVENTORY_APPLICABLE == "no" ? false : true;
                                    $.each(productSummaryList, $.proxy(function (index, item) {
                                        var obj = {
                                            ProductName: vm.MakeNormalArray(item.product_name),
                                            ProductList: [],
                                            ProductDescription: item.item_product_desc,
                                            ProductQuantity: item.item_product_Quantity,
                                            ProductInStock: (parseInt(item.inventory_available_quantity)),//+ parseInt(item.item_product_Quantity)),
                                            ProductRate: item.item_product_list_price,
                                            ProductTax: 0,
                                            ProductAmount: item.item_product_amount,
                                            ProductDiscount: item.item_product_discount,
                                            ProductDiscountType: item.item_product_discount_type,
                                            ProductDiscountValue: item.item_product_discount_value,
                                            ProductItemTotal: item.item_total,
                                            PriceBookId: item.item_price_book_id,
                                            toggleDiscount: false,
                                            toggleTax: false,
                                            TaxData: (item.tax_data != null) ? vm.MakeNormalArray(item.tax_data) : [],
                                            DiscountData: [],
                                        };
                                        productSummary.push(obj);
                                    }, vm));
                                }
                                vm.ProductSummary = {
                                    SummaryData: productSummary
                                }
                            });
                        }
                    }
                }
            })
        },
        CloneBillingAddress: function (event, type) {
            if (typeof (type) != "undefined") {
                if (type != 'loadtax') {
                    this.FillDropdown(type, event.target.value);
                }
                if (type == "ShippingZipCode" || type == "ShippingCity" || type == "ShippingState" || type == 'loadtax') {
                    this.CalculateTax();
                }
            }
            else {
                if ($("#chkCloneBillingAddress").prop("checked")) {
                    this.Address.ShippingAddress.ShippingContactPerson = this.Address.BillingAddress.BillingContactPerson
                    this.Address.ShippingAddress.ShippingZipCodeList = this.Address.BillingAddress.BillingZipCodeList;
                    this.Address.ShippingAddress.ShippingCityList = this.Address.BillingAddress.BillingCityList;
                    this.Address.ShippingAddress.ShippingStateList = this.Address.BillingAddress.BillingStateList;
                    this.Address.ShippingAddress.ShippingAccountAddress = this.Address.BillingAddress.BillingAccountAddress;
                    this.Address.ShippingAddress.ShippingAccountAddress2 = this.Address.BillingAddress.BillingAccountAddress2;
                    this.Address.ShippingAddress.ShippingCity = this.Address.BillingAddress.BillingCity;
                    this.Address.ShippingAddress.ShippingZipCode = this.Address.BillingAddress.BillingZipCode;
                    this.Address.ShippingAddress.ShippingState = this.Address.BillingAddress.BillingState;
                    this.Address.ShippingAddress.ShippingCountry = this.Address.BillingAddress.BillingCountry;
                    this.Address.ShippingAddress.ShippingAccountCity = this.Address.BillingAddress.BillingAccountCity;
                    this.Address.ShippingAddress.ShippingCompanyName = this.Address.BillingAddress.BillingCompanyName;
                }
            }
        },
        CloneShippingAddress: function (event, type) {
            if (typeof (type) != "undefined") {
                if (type != 'loadtax') {
                    this.FillDropdown(type, event.target.value);
                }
                if (type == "ShippingZipCode" || type == "ShippingCity" || type == "ShippingState" || type == 'loadtax') {
                    this.CalculateTax();
                }
            }
            else {
                if ($("#chkCloneBillingAddress").prop("checked")) {
                    this.Address.ShippingAddress.ShippingContactPerson = this.Address.BillingAddress.BillingContactPerson;
                    this.Address.ShippingAddress.ShippingZipCodeList = this.Address.BillingAddress.BillingZipCodeList;
                    this.Address.ShippingAddress.ShippingCityList = this.Address.BillingAddress.BillingCityList;
