                                    projectId = item.ProductName[0];
                                }
                            }
                            else {
                                projectId = item.ProductName == null ? '' : item.ProductName.value;
                            }
                        } else {
                            projectId = item.ProductName;
                        }

                        if (typeof (projectId) != 'undefined' || item.ProductName != '') {
                            if (projectId.length > 0 || projectId != 0) {
                                var dataObject = {
                                    ProductName: projectId,
